import 'react-dropzone-uploader/dist/styles.css'
import Dropzone, {useDropzone} from 'react-dropzone-uploader'
import styled from 'styled-components'
import { useContext } from 'react'
import { TranslationContext } from '../store/TranslationContext'

const StyledComponent = styled.div`
padding: 5px;
border: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color);
border-radius: 10px;
.dzu-dropzone{
  z-index: 0;
  overflow: auto;
  border: none;
  background-color: var(--white);
  min-height: unset;
}
.dzu-inputLabelWithFiles{
  color: var(--theme);
  background-color: var(--white);
  border: var(--theme) 2px solid;
  margin: 10px auto 20px auto;
  border-radius: 50%;
  font-size: 30px;
}
.dzu-inputLabelWithFiles:hover,
.dzu-inputLabelWithFiles:active,
.dzu-inputLabelWithFiles:focus{
  color: var(--white);
  background-color: var(--theme);
}
.dzu-inputLabel{
  display: none;
}
.dzu-submitButtonContainer{
  display: none;
}
.dzu-submitButton {
  background-color: var(--theme);
}
.dzu-previewContainer{
  padding: 10px;
  margin: 10px;
  align-items: unset;
  width: -webkit-fill-available;
  border: var(--bs-border-width) var(--bs-border-style) var(--bs-border-color);;
  border-radius: 5px;
  display: flex;
  min-width: unset;
}
.dzu-previewStatusContainer{
  align-items: unset;
  position: absolute;
  top: 5px;
  right: 5px;
  border: 2px solid #333333;
  border-radius: 4px;

}
progress{
  display: none;
}
.dzu-previewButton{
    background-size: 20px 20px;
    width: 20px;
    height: 20px;
    margin: 2px 1px 1px 2px;
    opacity: 1;
}
.dzu-previewImage {
    max-height: 200px;
    max-width: 200px;
    padding: 0cqmax;
    width: unset;
    margin-top: 30px;
    border: inherit;
}

`

const ImagesUploader = ({images, setImages, articleImagesCount=0}) => {
  const handleChangeStatus = ({file}, status) => {
    if(status === 'done') console.log(file)
    if (status === 'done') setImages((prevState)=>[...prevState, file])
    if (status === 'removed') setImages((prevState)=>prevState.filter((f)=>f !== file))
  }
  const uploadImages = ()=>{
    if (images.length === 0){
      document.querySelector('.dzu-inputLabel').click()
    }
    
  }
  const {translate} = useContext(TranslationContext)
  return (
    <>
      <StyledComponent onClick={uploadImages} className={images.length === 0 ? 'cursor-pointer':''}>
          <Dropzone
            inputContent=""
            inputWithFilesContent='+'
            maxFiles={20-articleImagesCount}
            onChangeStatus={handleChangeStatus}
            accept="image/*"
          />
          { images.length === 0 &&
            <div className='text-center pt-3 d-flex align-items-center justify-content-center' style={{height: 120}}><i style={{fontSize:100}} className="fa-solid fa-cloud-arrow-up fa-2xl color-theme"></i></div>
          }
          <h6 className='text-center color-theme p-3'>{ images.length + articleImagesCount === 0 ? translate('Upload up to 20 images') : `${20 - images.length - articleImagesCount}` + translate('images left') }</h6>
      </StyledComponent>
    </>
    
  )
}
export default ImagesUploader