import React, { useContext } from 'react'
import SelectWithIcons from '../../Components/SelectWithIcons'
import { BrowserContext } from '../../store/BrowserContext'
import { TranslationContext } from '../../store/TranslationContext'

const CategerySelect = ({category, setCtegory, emptySelection}) => {
    const {browserData} = useContext(BrowserContext)
    let options = browserData.categories.sort((a, b) => a.order - b.order).map(cat=>{return{value: cat.id, label: cat.name, icon: cat.icon}})

    options = [null, ...options]
    const {translate}=useContext(TranslationContext)
    return (
        <div className='p-2'>
            <label className='form-label'><strong>{translate('Category')}</strong></label>
            <SelectWithIcons showSearch={false} value={category} setValue={setCtegory} options={options} emptySelection={emptySelection} />
        </div>
    )
}

export default CategerySelect