import React, { useContext, useEffect } from 'react'
import { BrowserContext } from '../../store/BrowserContext'
import SelectWithIcons from '../../Components/SelectWithIcons'
import { TranslationContext } from '../../store/TranslationContext'

const BrandSelect = ({brand, setBrand, category ,emptySelection}) => {
    const {browserData} = useContext(BrowserContext)
    const brands = browserData.brands
    let options = []
    brands.forEach((elem)=>{
        if ((category != -1 && browserData.categories.find(elem=>elem.id==category).brands.includes(elem.id)) || category == -1){
            if (elem.id != 93)  options = [
                ...options
                ,{
                    value : elem.id,
                    icon : elem.icon,
                    label : elem.name
                }
            ]
        }
        
    })
    const anotherBrand = browserData.brands.find(elem=>elem.id==93)
    const sortedOptions = options.sort((a, b) =>  a.label.localeCompare(b.label))
    options = [...sortedOptions, {
        value : anotherBrand.id,
        icon : anotherBrand.icon,
        label : 'علامة أخرى'
    }]
    useEffect(()=>{
        if (emptySelection){ 
            const cat = browserData.categories.find(elem=>elem.id == category)
            if ( category != -1 && !cat.brands.includes(brand)){
                if (options.length > 1 ){
                    const firstBrand = options.filter(elem=> elem && !elem.disabled)[0]
                    setBrand(firstBrand.value)
                }
                else{
                    setBrand(93)
                }
                
            }
        }
    }, [category])
    const {translate}=useContext(TranslationContext)
    return (
        <div className='p-2'>
            <label className='form-label'><strong>{translate('Brand')}</strong></label>
            <SelectWithIcons value={brand} setValue={setBrand} options={options} emptySelection={emptySelection} />
        </div>
    )
}

export default BrandSelect