import React, { useContext, useEffect, useRef, useState } from 'react'
import {BrowserContext} from '../../store/BrowserContext'
import {UserContext} from '../../store/UserContext'
import { Message } from '../../Components/Message'
import { Loader } from '../../Components/Loader'

import FormContainer from '../../Components/FormContainer'
import axios from 'axios'
import { isPhoneNumber, scrollToTop } from '../../utils'
import { reduceImageQuality } from '../../utils'
import { useNavigate, useParams } from 'react-router-dom'
import { MultiSelect } from "react-multi-select-component";
import ImagesUploader from '../../Components/ImagesUploader'
import CategerySelect from '../Search/CategerySelect'
import BrandSelect from '../Search/BrandSelect'
import { TranslationContext } from '../../store/TranslationContext'


const currentDate = new Date();
const currentYear = currentDate.getFullYear();
const yearsList = Array.from({length: currentYear - 1900 + 1}, (_, i) => 1900 + i)


const EditArticleScreen = () => {
  const {translate} = useContext(TranslationContext)
  /////////////////////
  //
  let {browserData, setGlobalMessage, transformedParameters} = useContext(BrowserContext)
  ///////////////////////////////////
  //

  const [step, setStep] = useState(1)
  const {userData} =useContext(UserContext)
  const navigate = useNavigate()

  const {articleId} = useParams() 
  const [createArticleResponse, setCreateArticleResponse] = useState() 


  const [articleImages, setArticleImages] = useState([])
  const [mainImageId, setMainImageId] = useState(0)
  const [loadingArticle, setLoadingArticle] = useState(false)
  const titleRef = useRef()
  useEffect(()=>{
    if (articleId){
      setLoadingArticle(true)
      axios.get(
        '/api/dashboard/get-article-for-edit/?article-id=' + articleId,
        {
          headers : {
            "Content-Type": "multipart/form-data",
            Authorization : 'Bearer ' + userData.token
          }
        }
      ).then((response)=>{
        titleRef.current.classList.remove('error')
        let data = response.data
        setState(data.state_code)
        setCity(data.city)
        setTitle(data.title)
        setBrand(Number(data.brand_id))
        setOtherBrand(data.other_brand)
        setCategory(Number(data.category_id))
        setOtherCategory(data.other_category)
        setYear(data.year)
        setColor(data.color_id)
        setDocument(data.document_id)
        setEngine(data.engine)
        setPrice(data.price)
        setOfferedPrice(data.offered_price)
        setCarCondition(data.is_used ? 'Used' : 'New')
        setMileage(data.mileage)
        setFuel(data.fuel_id) 
        setGearBox(data.gear_box_id)
        setExchange(data.exchange)
        setAllOptionsCar(data.is_all_options)
        setCarOptions(transformedParameters.options.filter(option=>data.options.includes(option.value)))
        setDescription(data.description)
        const [pn_1, pn_2, pn_3] = data.phone_numbers_list
        pn_1 && setPhoneNumber1(pn_1);
        pn_2 && setPhoneNumber2(pn_2);
        pn_3 && setPhoneNumber3(pn_3);
        setArticleImages(data.images_list)
        setMainImageId(data.main_image_id)
        setLoadingArticle(false)
      }).catch((error)=>{
        setLoadingArticle(false)
        setError(error)
      })
    }
      
  }, [articleId])

  
  const [state, setState] = useState('16')
  const [city, setCity] = useState('')
  const [title, setTitle] = useState('')
  const [brand, setBrand] = useState(1)
  const [other_brand, setOtherBrand] = useState('')
  const [category, setCategory] = useState(1)
  const [other_category, setOtherCategory] = useState('')
  const [year, setYear] = useState(currentYear)
  const [color, setColor] = useState('1')
  const [document, setDocument] = useState('1')
  const [engine, setEngine] = useState('')
  const [price, setPrice] = useState(0)
  const [offered_price, setOfferedPrice] = useState(0)
  const [car_condition, setCarCondition] = useState('Used')
  const [mileage, setMileage] = useState(0)
  const [fuel, setFuel] = useState('1') 
  const [gear_box, setGearBox] = useState('1')
  const [exchange, setExchange] = useState(false)
  const [all_options_car, setAllOptionsCar] = useState(false)
  const [car_options, setCarOptions] = useState([])
  const [description, setDescription] = useState('')

  const [phone_number_1, setPhoneNumber1] = useState('')
  const [phone_number_2, setPhoneNumber2] = useState('')
  const [phone_number_3, setPhoneNumber3] = useState('')


  const [error, setError] = useState(null)
  const [loading, setLoading] = useState(false)

  const cityChangeHandler = (e)=>{
    let newCity = e.target.value
    setCity(newCity)
  }
  const phoneNumber1ChnageHanlder =(e)=>{
    let newPhoneNumber1 = e.target.value
    if (isPhoneNumber(newPhoneNumber1)){
      setPhoneNumber1(newPhoneNumber1)
    }
  }
  const phoneNumber2ChnageHanlder =(e)=>{
    let newPhoneNumber2 = e.target.value
    if (isPhoneNumber(newPhoneNumber2)){
      setPhoneNumber2(newPhoneNumber2)
    }
  } 
  const phoneNumber3ChnageHanlder =(e)=>{
    let newPhoneNumber3 = e.target.value
    if (isPhoneNumber(newPhoneNumber3)){
      setPhoneNumber3(newPhoneNumber3)
    }
  } 

  const titleChangeHandler = (e)=>{
    setTitle(e.target.value)
  }
  
  const yearChangeHandler = (e)=>{
    let newYear = e.target.value
    setYear(newYear)
  }
  const colorChangeHandler = (e)=>{
    let newColor = e.target.value
    setColor(newColor)
  }
  const documentChangeHandler = (e)=>{
    let newDocument = e.target.value
    setDocument(newDocument)
  }
  const engineChangeHandler = (e)=>{
    let newEngine = e.target.value
    setEngine(newEngine)
  }
  const priceChangeHandler = (e)=>{
    let newPrice = e.target.value
    setPrice(newPrice)
  }
  const offredPriceChangeHandler = (e)=>{
    let newOffredPrice = e.target.value
    setOfferedPrice(newOffredPrice)
  }
  const carConditionChangeHandler = (e)=>{
    let newCarCondtion = e.target.value
    setCarCondition(newCarCondtion)
  }
  const mileageChangeHandler = (e)=>{
    let newMileage = e.target.value
    setMileage(newMileage)
  }
  const fuelChangeHandler = (e)=>{
    let newFuel = e.target.value
    setFuel(newFuel)
  }
  const gearBoxChangeHandler = (e)=>{
    let newGearBox = e.target.value
    setGearBox(newGearBox)
  }
  const exchangeChangeHandler = (e)=>{
    let newExchange = e.target.checked
    setExchange(newExchange)
  }
  const allOptionsCarChangeHandler = (e)=>{
    setCarOptions([])
    let newAllOptionsCar = e.target.checked
    setAllOptionsCar(newAllOptionsCar)
  }
  const descriptionChangeHandler = (e)=>{
    let newDecription = e.target.value
    setDescription(newDecription)
  }
  const articleSubmitionHandler=async(e) =>{
    setCreateArticleResponse(null)
    setError(null)
    e.preventDefault()
    setLoading(true)
    axios.post(
      articleId ? '/api/dashboard/update-article/' : '/api/dashboard/create-article/',
      {
        state,
        city,
        title,
        brand,
        other_brand,
        category,
        other_category,
        year,
        color,
        document,
        engine,
        price,
        offered_price,
        car_condition,
        mileage,
        fuel,
        gear_box,
        exchange,
        all_options_car,
        car_options:car_options.map(option=>option.value),
        description,
        phone_number_1,
        phone_number_2,
        phone_number_3,
        images : await reduceImageQuality(images),
        article_id : articleId,
      },
      {
        headers : {
          "Content-Type": "multipart/form-data",
          Authorization : 'Bearer ' + userData.token
        }
      }
    ).then((response)=>{
      setLoading(false)
      setGlobalMessage({
        text : response.data.detail.message,
        className : 'my-4 mx-3',
        variant : 'success'
      })
      navigate(response.data.detail.url)
    }).catch((error)=>{
      setLoading(false)
      setError(error)
    })
  }
  
  const nextHandler = (e)=>{
    e.preventDefault()
    if (step <= 4){
      if(step < 4){
        scrollToTop()
        setStep((prevState)=> prevState +1)
      }else{
        articleSubmitionHandler(e)
      }
    }
  }
  const prevHandler = (e)=>{
    e.preventDefault()
    if (step >= 2){
      setStep((prevState)=> prevState - 1)
    }
    scrollToTop()
  }

  const customStrings = {
    allItemsAreSelected:translate("All options are selected"),
    clearSearch:translate("Clear Search"),
    clearSelected:translate("Clear Selected options"),
    noOptions:translate("No options"),
    search:translate("Search"),
    selectAll:translate("Select all"),
    selectAllFiltered:translate("Select all (filtered)"),
    selectSomeItems:translate("Select..."),
    create:translate("Create")
  };
  const [images, setImages]=useState([])

  const [deleteLoading, setDeleteLoading] = useState(false)
  const deleteImage =(image)=>{
    setDeleteLoading(true)
    axios.post('/api/dashboard/delete-image/',
    {
      image_id : image,
      article_id : articleId
    },
    {
      headers : {
        "Content-Type": "multipart/form-data",
        Authorization : 'Bearer ' + userData.token
      }
    }
    ).then(response=>{
      setArticleImages(prev=>prev.filter(image => image.id != response.data))
      setDeleteLoading(false)
    }).catch(error=>{
      setDeleteLoading(false)
    })
  }
  const setMainImage=(mainImageId)=>{
    setDeleteLoading(true)
    axios.post('/api/dashboard/set-main-image/',
    {
      image_id : mainImageId,
      article_id : articleId
    },
    {
      headers : {
        "Content-Type": "multipart/form-data",
        Authorization : 'Bearer ' + userData.token
      }
    }
    ).then(response=>{
      setMainImageId(response.data)
      setDeleteLoading(false)
    }).catch(error=>{
      setDeleteLoading(false)
    })
  }
  const {Left, Right, S} = useContext(BrowserContext)

  const categoryRef = useRef()
  const brandRef = useRef()

  let step2Error = false
  if (!title.trim()){
    titleRef.current && titleRef.current.classList.add('error')
    step2Error = true
  }
  else titleRef.current && titleRef.current.classList.remove('error')

  if (category == 10 && other_category.trim() == ''){
    categoryRef.current && categoryRef.current.classList.add('error')
    step2Error = true
  }
  else categoryRef.current && categoryRef.current.classList.remove('error')

  if (brand == 93 && other_brand.trim() == ''){
    brandRef.current && brandRef.current.classList.add('error')
    step2Error = true
  }
  else brandRef.current && brandRef.current.classList.remove('error')

  const browserContext = useContext(BrowserContext)
  useEffect(()=>{
    articleId ? browserContext.setTitle(translate('Edit article') + ` | ${translate('Ch7al Machya')}`) : browserContext.setTitle(translate('Create article') + ` | ${translate('Ch7al Machya')}`)
    browserContext.setDescription(translate('description'))
  },[])
  return (
    <>
        <div hidden={!loadingArticle} className='container mw-900'>
          <div className='card mt-5 py-5'><Loader className='color-theme my-5 m-auto' diam={80} /></div>
        </div>
        <div hidden={loadingArticle} className='pb-5'>
          <FormContainer className='mt-5 mw-900'>
            { error && <Message variant='danger' removeAlert={()=>{setError(null)}} >{error.response.data.detail || error.message}</Message> }
            { createArticleResponse && <Message variant='success' removeAlert={()=>{setCreateArticleResponse(null)}} >{createArticleResponse}</Message> }
            <div hidden={step !== 1} className='d-flex flex-row flex-wrap'>
                <div id='state' className='form-group mt-2 col-12 col-sm-6 p-2'>
                    <label className='form-label'><strong>{translate('State')}</strong></label>
                    <select className='form-select' onChange={(e)=> setState(e.target.value)} value={state}>
                      { browserData.states.map(state => <option key={state.code} value={state.code}>{state.code} {state.name}</option>) }
                    </select>
                </div>
                <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                    <label className='form-label'><strong>{translate('City')}</strong></label>
                    <input 
                        className='form-control'
                        type='text'
                        placeholder={translate('Enter city')}
                        value={city}
                        onChange={cityChangeHandler}
                        maxLength={50}
                    >
                    </input>
                </div>
                <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                    <label className='form-label'><strong>{translate('Phone number')} 1</strong></label>
                    <input 
                        className='form-control'
                        type='tel'
                        placeholder={translate('You can keep this field empty')}
                        value={phone_number_1}
                        onChange={phoneNumber1ChnageHanlder}
                    >
                    </input>
                </div>
                <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                    <label className='form-label'><strong>{translate('Phone number')} 2</strong></label>
                    <input 
                        className='form-control'
                        type='tel'
                        placeholder={translate('You can keep this field empty')}
                        value={phone_number_2}
                        onChange={phoneNumber2ChnageHanlder}
                    >
                    </input>
                </div>
                <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                    <label className='form-label'><strong>{translate('Phone number')} 3</strong></label>
                    <input 
                        className='form-control'
                        type='tel'
                        placeholder={translate('You can keep this field empty')}
                        value={phone_number_3}
                        onChange={phoneNumber3ChnageHanlder}
                    >
                    </input>
                </div>
            </div>
            <div hidden={step !== 2}>
              <div className='col-12 col-sm-6'><CategerySelect emptySelection={true} category={category} setCtegory={setCategory}/></div>
              <div className={`d-flex flex-row flex-wrap p${S}-4 col-12 col-sm-6`} hidden={category != 10}>
                  <div className='form-group mt-2 p-2 flex-grow-1' ref={categoryRef}>
                      <input 
                          className='form-control'
                          type='text'
                          placeholder={translate('Enter category name')}
                          value={other_category}
                          onChange={(e)=>{setOtherCategory(e.target.value)}}
                          maxLength={50}
                      >
                      </input>
                  </div>
              </div>
              <div className='col-12 col-sm-6'><BrandSelect emptySelection={true} category={category} setBrand={setBrand} brand={brand}/></div>
              <div hidden={brand != 93 } className={`d-flex flex-row flex-wrap p${S}-4 col-12 col-sm-6`}>
                  <div className='form-group mt-2 p-2 flex-grow-1' ref={brandRef}>
                      <input 
                          className='form-control'
                          type='text'
                          placeholder={translate('Enter brand name')}
                          value={other_brand}
                          onChange={(e)=>{setOtherBrand(e.target.value)}}
                          maxLength={50}
                      >
                      </input>
                  </div>
              </div>
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2' ref={titleRef}>
                      <label className='form-label'><strong>{translate('Model')} *</strong></label>
                      <input 
                          className='form-control'
                          type='text'
                          placeholder={translate('Enter car model')}
                          value={title}
                          onChange={titleChangeHandler}
                          maxLength={50}
                      >
                      </input>
                  </div>
              </div>     
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                      <label className='form-label'><strong>{ translate('Year') }</strong></label>
                      <select className='form-select' value={year} onChange={yearChangeHandler}>
                        {yearsList.map((year)=><option key={year}>{year}</option>)}
                      </select>
                  </div>
              </div>
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                      <label className='form-label'><strong>{translate('Color')}</strong></label>
                      <select className='form-select' value={color} onChange={colorChangeHandler}>
                        { browserData.colors.map((color)=><option key={color.id} value={color.id}>{ color.name }</option>) }
                      </select>
                  </div>
              </div>
              
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                      <label className='form-label'><strong>{translate('Engine')}</strong></label>
                      <input 
                          className='form-control'
                          type='text'
                          placeholder={translate('Enter engine detail')}
                          value={engine}
                          onChange={engineChangeHandler}
                          maxLength={50}
                      >
                      </input>
                  </div>
              </div>
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                      <label className='form-label'><strong>{translate('Car condtion')}</strong></label>
                      <select className='form-select' value={car_condition} onChange={carConditionChangeHandler}>
                          <option>{translate('Used')}</option>
                          <option>{translate('New')}</option>
                      </select>
                  </div>
              </div>
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                      <label className='form-label'><strong>{ translate('Mileage (in KM)') }</strong></label>
                      <input 
                          className='form-control'
                          type='number'
                          value={mileage}
                          onChange={mileageChangeHandler}
                          min={0}
                      >
                      </input>
                  </div>
              </div>
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                      <label className='form-label'><strong>{ translate('Fuel') }</strong></label>
                      <select className='form-select' value={fuel} onChange={fuelChangeHandler}>
                          { browserData.fuels.map(fuel=><option key={fuel.id} value={fuel.id}>{ fuel.name }</option>) }
                      </select>
                  </div>
              </div>
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                      <label className='form-label'><strong>{ translate('Gearbox') }</strong></label>
                      <select className='form-select' value={gear_box} onChange={gearBoxChangeHandler}>
                      { browserData.gear_boxs.map(gear=><option key={gear.id} value={gear.id}>{ gear.name }</option>) }
                      </select>
                  </div>
              </div>
              <hr/>
              <div className="form-group d-flex m-3  align-items-center">
                <strong className='fs-5'>{translate('Is it all options?')}</strong>
                <input className="mx-3" type="checkbox" checked={all_options_car} onChange={allOptionsCarChangeHandler}/>
              </div>
              {!all_options_car && 
                <div className='d-flex flex-row flex-wrap'>
                    <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                        <label className='form-label'><strong>{translate('What are its options ?')}</strong></label>
                        <MultiSelect className='hide-search' overrideStrings={customStrings} options={browserData.options.map(({id, name})=>({label : name, value : id}))} labelledBy="Select" value={car_options} onChange={setCarOptions}/>
                    </div>
                </div>
              }
              <hr/>
              <div className='d-flex flex-row flex-wrap'>
                <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                    <label className='form-label'><strong>{translate('Document')}</strong></label>
                    <select className='form-select' value={document} onChange={documentChangeHandler}>
                    { browserData.documents.map(document=><option key={document.id} value={document.id}>{ document.name }</option>) }
                    </select>
                </div>
              </div>
              <div className="form-group d-flex m-3  align-items-center">
                <strong className='fs-5'>{translate('Open for exchange?')}</strong>
                <input className="mx-3" type="checkbox" checked={exchange} onChange={exchangeChangeHandler}/>
              </div>
            </div>
            <div hidden={step !== 3}>
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                      <label className='form-label'><strong>{translate('Price (in Millions)')}</strong></label>
                      <input 
                          className='form-control'
                          type='number'
                          value={price}
                          onChange={priceChangeHandler}
                          min={0}
                      >
                      </input>
                  </div>
              </div>
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12 col-sm-6 p-2'>
                      <label className='form-label'><strong>{translate('Offred Price (in Millions)')}</strong></label>
                      <input 
                          className='form-control'
                          type='number'
                          value={offered_price}
                          onChange={offredPriceChangeHandler}
                          min={0}
                      >
                      </input>
                  </div>
              </div>
              <hr/>
              <div className='d-flex flex-row flex-wrap'>
                  <div className='form-group mt-2 col-12  p-2'>
                      <label className='form-label'><strong>{ translate('Description') }</strong></label>
                      <textarea 
                          rows={5}
                          className='form-control'
                          type='text'
                          value={description}
                          onChange={descriptionChangeHandler}
                      >
                      </textarea>
                  </div>
              </div>
            </div>
            <div hidden={step !== 4}>
              {articleId && 
              <>
                <h3 className='mb-4 color-theme'>{translate('Current Images')}</h3>
                <div className='d-flex mb-5 border rounded-3 p-3 flex-wrap gap-2 justify-content-around'>
                  { articleImages.find(image=>image.id == mainImageId) && <div className='mb-5'>
                    <div className='square border rounded-2' style={{width: 200, marginTop: 54}} >
                    <button type='btn' style={{ [Left] : 5, top: 5}} className='btn btn-success btn-sm position-absolute z-1'> <i className="fa-solid fa-thumbtack fa-xl"/></button>
                      <img className={`rounded-1 ${ deleteLoading && ' blur' }`} src={`/resized-image/200/?path=${articleImages.find(image=>image.id == mainImageId).url.slice(1)}`}/>
                    </div>
                  </div>}
                  {articleImages.filter(image=>image.id != mainImageId).map(image=>{
                    return (
                      <div key={image.id} className='mb-5'>
                        <div className='d-flex justify-content-between'>
                          <div>{image.id != mainImageId && <button disabled={deleteLoading} onClick={()=>{ setMainImage(image.id) }} className='btn btn-outline-success m-2 p-1 px-2 rounded-1 border-0'><i className="fa-solid fa-thumbtack fa-xl"/></button>}</div>
                          <div><button hidden={articleImages.length == 1} type='button' disabled={deleteLoading} onClick={()=>deleteImage(image.id)} className='m-2 btn btn-outline-danger p-1 rounded-1 border-0' ><i className="fa-solid fa-trash fa-xl"></i></button>                        </div>
                        </div>
                        <div className={`square border rounded-2 ${ image.id == mainImageId && 'overlow-hidden border-3 border-success' }`} style={{width: 200}} key={image.id}>
                          <img className={`rounded-2 ${ deleteLoading && ' blur' }`} src={`/resized-image/200/?path=${image.url.slice(1)}`}/>
                        </div>
                      </div>
                    )
                  })}
                </div>
                <h3 className='mb-4 mt-5 color-theme'>{translate('Add Images')}</h3>
              </>  
              }
              <ImagesUploader articleImagesCount={articleImages.length} images={images} setImages={setImages}/>
            </div> 
            <div className='d-flex col-12 py-4'>
            {step >= 2 && 
              <button type='submit' className='w-fit-content d-flex btn btn-success' disabled={loading} onClick={prevHandler}>
                <span><i className={`fa-solid fa-chevron-${Left}`}></i> {translate('Go back')}</span>
              </button>
            }
            <button type='submit' disabled={(step === 2 && step2Error) || ( step === 4 && (images.length + articleImages.length )=== 0 )  || loading} className='w-fit-content d-flex btn btn-success margin-inline-start-auto' onClick={nextHandler}>
              { step === 4 ? 
                (!loading ? <>{ articleId ? translate('Update article'): translate('Create article') }</> : <>{translate('Getting Created')} <Loader className="mx-2" diam={23}/></>) :
                <span>{translate('Next')} <i className={`fa-solid fa-chevron-${Right}`}></i></span> 
              } 
            </button>
              
            </div> 

          </FormContainer>
                    
        </div>
        
      </>
  )
}

export default EditArticleScreen