import React, { useContext, useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import LazyImage from '../Components/UI/LazyImage'
import {TranslationContext} from '../store/TranslationContext'

const Select = styled.div`
padding-inline-start: 10px;
padding-inline-end: 10px;
padding-top:7px;
padding-bottom: 7px;
border-radius: 4px;
cursor: default;
`
const OptionsContainer = styled.div`
padding-top: 1px;
.option:hover:not(.disabled),
.option:active:not(.disabled){
    background-color: var(--theme);
    color: var(--white);
}
.option{    
    padding: 8px;
}
.option.disabled{
    padding: 8px !important;
    opacity: 0.2;
}

cursor: default;
`

const SelectWithIcons = ({value, setValue, options, showSearch=true, emptySelection=false}) => { 
    useEffect(()=>{
        setValue(value)
    }, [])
    const [clicked, setClicked] = useState(false)
    let d
    clicked ? d="M18 15 12 9 6 15" : d="M6 9L12 15 18 9"

    const targetRef = useRef();
    useEffect(() => {
        const handleGlobalClick = (event) => {
            const target = event.detail.target
            if (!targetRef.current.contains(target) && !optionsContainerRef.current.contains(target) ) {
                setClicked(false);
            }else if(targetRef.current.contains(target) && !closeButtonRef.current.contains(target)){
                setClicked(prev=>!prev);
            }
        };
        document.addEventListener('global-click', handleGlobalClick);
        return () => {
          document.removeEventListener('global-click', handleGlobalClick);
        };
    }, [])
        
    const selectedOption = options && options.find(option=>option && (option.value == value))
    const selectedOptionLabel = selectedOption ?  selectedOption.label : '' 
    const closeButtonRef = useRef()
    const optionsContainerRef = useRef()
    const [searchText, setSearchText] = useState('')
    const finalOptions = options.filter(option => option && (!option.disabled || !searchText.trim()) && option.label.toLowerCase().includes(searchText.toLowerCase().trim()))
    const {translate} = useContext(TranslationContext)
    return (
        <>
            <Select ref={targetRef} className='bg-color-white border d-flex justify-content-between'>
                <div>{selectedOptionLabel}</div>
                <div>
                    <svg hidden={!selectedOption || emptySelection} className='cursor-pointer' ref={closeButtonRef} width="24" height="24" fill="none" stroke="currentColor" strokeWidth="2" onClick={(e)=>{setClicked(false) ;setValue(-1)}}>
                        <line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"/>
                    </svg>
                    <svg width="24" height="24" fill="none" stroke="currentColor" strokeWidth='2' ><path d={d}></path></svg>
                </div>
            </Select>
            <OptionsContainer hidden={!clicked} >
                <div className='position-relative' style={{margin: '0 2px'}}>
                    <div ref={optionsContainerRef} className='position-absolute bg-color-white z-1 w-100 rounded-inheret border' >
                        { showSearch && options.length > 1 && 
                            <>
                                <div className='d-flex align-items-center'>
                                    <input placeholder='Recharche' className='search-area flex-grow-1 p-2' rows={1} value={searchText} onChange={ (e)=>setSearchText(e.target.value) }/>
                                    <svg hidden={searchText.trim() == ''} className='cursor-pointer mx-1' width="24" height="24" fill="none" stroke="currentColor" strokeWidth="2" onClick={(e)=>{setSearchText('')}}>
                                        <line x1="18" y1="6" x2="6" y2="18"></line><line x1="6" y1="6" x2="18" y2="18"/>
                                    </svg>
                                </div>
                                <hr className='m-0 border-black'/>
                            </>
                                
                        }
                        <div style={{maxHeight:'40vh', overflowY: 'auto'}}>
                            {finalOptions.map(option=> option &&
                                <React.Fragment key={option.value}>
                                    <div className={`${option.disabled ? 'disabled ' : ''}option d-flex gap-2 align-items-center`} onClick={()=>{ if(!option.disabled) {setSearchText('') ;setValue(option.value); setClicked(false)}}}>
                                        <LazyImage src={option.icon} width='30'/>
                                        <span>{option.label}</span>
                                    </div>
                                </React.Fragment>
                            )}
                        </div>
                        {finalOptions.length == 0 && 
                            <p className='text-center flex-grow-1 py-2 my-1'>{translate('No option was found')}</p>
                        }
                    </div>
                </div>
                    
            </OptionsContainer>
        </>
        
    )
    
};

export default SelectWithIcons;
